import { createApp } from 'vue';
import App from './App.vue';
import * as BoidsModule from './wasm/build/wasm_boids.js';
import wasmUrl from './wasm/build/wasm_boids.wasm';

let wasmModule = null;
console.log("wasmUrl:", wasmUrl); //  URL 

BoidsModule.default({
    locateFile: (path) => {
        if (path.endsWith('.wasm')) {
            return wasmUrl; // URL
        }
        return path;
    },
}).then(Module => {
    wasmModule = Module;
    console.log('Wasm module initialized:', Module);

    // Vue  WebAssembly 
    const app = createApp(App);
    app.provide('wasmModule', Module);
    app.mount('#app');
});

if ('serviceWorker' in navigator) {
    navigator.serviceWorker.register('/coi-serviceworker.js').then(() => {
        console.log('Service Worker registered for COOP/COEP headers.');
    }).catch((error) => {
        console.error('Service Worker registration failed:', error);
    });
}